@mixin laptop{
	@media screen and (max-width: 1199px){
		@content;
	}
}
@mixin tablet{
	@media screen and (max-width: 949px){
		@content;
	}
}
@mixin phone {
@media screen and (max-width: 768px){
		@content;
	}
}
@mixin phone-small {
	@media screen and (max-width: 599px){
			@content;
		}
	}
@mixin mobile {
	@media screen and (max-width: 480px){
		@content;
	}
}